.recipe{
    border-radius: 10px;
    box-shadow:0px 5px 20px rgb(71,71,71);
    margin:20px;
    display:flex;
    flex-direction:column;
    justify-content: space-around;
    background: rgb(115, 224, 135);
    align-items: center;
    min-width:40%;
}

.image{
    border-radius: 30%;
    width:200px;
    height:200px;
    margin-bottom: 10px;
}